___CSS_LOADER_EXPORT___.push([module.id, `/* Container for the entire "How It Works" section */
.how-it-works-section {
    padding: 40px 20px;
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
    background-color: #fefefe; /* or another color that fits your design */
  }
  
  /* Main Title */
  .hiw-title {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #233535; /* Match your brand or text color */
  }
  
  /* Subtitle under the main title */
  .hiw-subtitle {
    font-size: 1rem;
    margin-bottom: 30px;
    color: #555;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
    line-height: 1.4;
  }
  
  /* Steps container */
  .hiw-steps-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 40px; /* Space between step cards */
  }
  
  /* Individual step card */
  .hiw-step {
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.08),
                0 4px 6px rgba(0, 0, 0, 0.04);
    width: 300px; /* Adjust as needed */
    padding: 20px;
    text-align: center;
    transition: transform 0.3s ease;
  }
  
  .hiw-step:hover {
    transform: translateY(-3px);
  }
  
  /* Container for the step number and title */
  .hiw-step-number-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 15px;
  }
  
  /* The circle that contains the step number */
  .hiw-step-number {
    background-color: #233535; /* Choose a brand color or another accent color */
    color: #fff;
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
    font-weight: bold;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 8px;
  }
  
  /* Step title text below the number */
  .hiw-step-title {
    font-size: 1.2rem;
    font-weight: 700;
    color: #233535;
    margin: 0;
  }
  
  /* Icon for each step */
  .hiw-step-icon {
    margin-bottom: 15px;
  }
  
  /* Step description text */
  .hiw-step-description {
    font-size: 0.95rem;
    color: #555;
    line-height: 1.4;
  }
  
  .hiw-contact-link {
    text-decoration: underline;
    color: inherit;
    font-weight: bold;
    transition: color 0.3s ease;
  }
  
  .hiw-contact-link:hover {
    color: #233535;
  }
